/* eslint-disable no-restricted-globals */

// This module defines a Web Worker script. Webpack uses the worker-loader to generate a separate file.
// Due to the confluence of webpack, worker-loader, and the way workers work, some ES2015 features like destructuring and modules
// don't work well.
const dexie = require('../../store/indexeddb').default;

// IE11
require('es6-promise/auto');

// TODO some way to cancel the worker so it doesn't keep crunching on large data sets. chunk-yield-wait?
self.onmessage = ({ data: { turn, query, productFamily } }) => {
    let db;
    try {
        db = dexie().infrastructureInventory;
    } catch (ex) {
        console.error(`${productFamily} object search requires IndexedDB support.`, ex);
        self.postMessage(JSON.stringify({ turn, error: ex, id: productFamily }));
        return;
    }

    db.where('productFamily')
        .equals(productFamily)
        .toArray()
        .then(results => results.reduce((memo, set) => {
            set.objects.forEach(({ id, name }) => {
                if (name && name.toString().toLowerCase().includes(query.toLowerCase())) {
                    memo[set.type] = memo[set.type] || [];
                    memo[set.type].push(id);
                }
            });
            return memo;
        }, {}))
        .then(results => self.postMessage(JSON.stringify({ turn, results, id: productFamily })))
        .catch(ex => self.postMessage(JSON.stringify({ turn, error: ex, id: productFamily })));
};
